/* src/app/features/ui-interface/forms/form-fileupload/form-fileupload.component.scss */
.custom-file-container__image-preview {
  box-sizing: border-box;
  transition: all 0.2s ease;
  margin-top: 54px;
  margin-bottom: 40px;
  height: 250px;
  width: 100%;
  border-radius: 4px;
  background-repeat: no-repeat;
  background-color: #fff;
  overflow: auto;
  padding: 15px;
  display: flex;
  justify-content: center;
}
.custom-file-container__image-preview .chooseImg {
  display: contents;
}
.custom-file-container__image-preview .chooseImg img {
  height: 100%;
}
.custom-file-container__image-preview .chooseImg span {
  background: #ffffff;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  text-align: center;
  margin-top: -6px;
  box-shadow: 0 4px 10px 0 rgba(51, 51, 51, 0.25);
  margin: 6px -28px;
  z-index: 9;
}
